.chartdropdown .chart-dropdown-item {
    /* display: block; */
    padding: 0.25rem 1rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    row-gap: 10px;
    text-align: inherit;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
    /* display: inline-flex; */
    flex-wrap: wrap;
    border-top: 1px solid transparent;
    border-right: 1px solid transparent;
    border-left: 1px solid transparent;
    border-image: initial;
    border-bottom: none;
    position: relative;
    list-style: none;
    margin: 3px 0px;
    cursor: pointer;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(255, 255, 255);
    color: rgb(23, 25, 36);
    box-shadow: rgb(239 242 245) 0px 1px 0px;
}
.chartdropdown .chart-dropdown-item:active {
  color: #fff;
  text-decoration: none;
  background-color: var(--theme-color-active);
}
.chartdropdown .chart-dropdown-menu {
    z-index: 99 !important;
    box-shadow: none;
    background: rgb(239, 242, 245);
    padding: 2px 3px;
    border-radius: 8px;
    margin: 39px 0px 0px -3px;
    /* margin-top: 38px; */
    align-items: center;
    position: absolute;
    flex-wrap: wrap;
}
.chartdropdown .chart-dropdown-toggle {
  padding: 4px 4px;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  border-image: initial;
  border-bottom: none;
  position: relative;
  list-style: none;
  cursor: pointer;
  float: right;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(255, 255, 255);
  color: rgb(23, 25, 36);
  box-shadow: rgb(239 242 245) 0px 1px 0px;
  margin: 0px 2px;
}
